import Link from 'next/link'

const Header_Style = {
    display : 'flex',
    justifyContent: 'left',
    alignItems: 'center',
    backgroundColor : '#999',
    height : '80px',
    minWidth : '600px',
    marginBottom : '20px',
}

const Logo_Style = {
  fontSize : '30px',
  marginLeft : '15px',
  textDecoration : 'none',
  color : 'black',
}

const Achievement_Style = {
  marginLeft : '40%',
  textDecoration : 'none',
  color : 'black',
}

function Header(){
  return(
    <header className="header" style={Header_Style}>

    <style jsx>{`
      .logo {
        fontSize : '30px',
        marginLeft : '15px',
        textDecoration : 'none',
        color : 'black',
      }

      .achievement {
        marginLeft : '40%',
        textDecoration : 'none',
        color : 'black',
      }
    `}</style>

      <Link href="/">
        <a className="logo">Todo Application</a>
      </Link>
      <Link href="/">
        <a className=" achievement" >나의 성취 보기</a>
      </Link>
    </header>
  )
}

export default Header
